<template>
  <div class="header">
    <span  class="iconfont icon-cebian"></span>
    <div v-for="(foot,i) in foots"  :key="i" class="iconfont">
            <router-link :to="{name:foot.name}">
                <i class="iconfont"  v-html="foot.icon" >  </i>
            </router-link>
        </div>
    <span class="iconfont icon-search" @click="goSearch"></span>
    <play></play>
  </div>
</template>

<script>
import play from './../views/paly.vue'
export default {
   data(){
        return {
            foots:[
                {path:"/index/mine",name:"mine",icon:"&#xe602;"},
                {path:"/index/singe",name:"singe",icon:"&#xe605;"},
                {path:"/index/singemv",name:"singemv",icon:"&#xe61f;"},
                
            ] 
        }
    },
    methods:{
        goSearch(){
            this.$router.push({name:'search'})
        }
    },
    components:{
        play,
    }

}
</script>
<style scoped>


.iconfont{font-size: 0.5rem;display: inline-block;color:#999;margin-left: 0.25rem;}
.header{background-color: red;padding: 10px;height:70px;line-height: 70px}
.icon-search{margin-left: 1.4rem;color: seashell;}
.icon-cebian{margin-right: 1.30rem;color: seashell;}
.router-link-active .iconfont{color:  seashell ;}
</style>





