

// 配置 路由  

import Vue from "vue";
import VueRouter from "vue-router";

// 0
Vue.use(VueRouter)

// 1. 定义组件
import Index from "./views/index.vue";
import Singe from "./views/singe.vue";
import Singemv from "./views/singmv.vue";
import Mine from "./views/mine.vue";
import App from "./components/app.vue";
import Search from "./views/search.vue";
import Singlist from "./views/singlist.vue";
import Video from "./views/video.vue";
import Play from "./views/paly.vue";

// 2. 配置 routes 
const routes = [
    {
        path:"", 
        redirect:{
            name:'mine'
        }
    },
    {
        path:"/index",
        name:"index",
        component:Index,
        children:[
            {path:"singe",name:'singe',component:Singe},
            {path:"singemv",name:'singemv',component:Singemv},
            {path:"mine",name:'mine',component:Mine},
            {path:"play",name:"play",component:Play},
        ]
    },
    {
        path:"/search",
        name:"search",
        component:Search
    },
    {
        path:"/singlist",
        name:"singlist",
        component:Singlist  
    },
    {
        path:"/video",
        name:"video",
        component:Video  
    },
    
    {
        path:"*",
        redirect:{
            name:'mine'
        }
    }
    

]

// 3. 创建 router
const router = new VueRouter({
    routes
})


// 4. 挂载 根实例 
export default router;



