<template>
    <div>
        <app></app>
        <keep-alive>
        <router-view class="routerview" :style="{'padding-bottom':bottom}"></router-view>
        </keep-alive>
        
    </div>
</template>

<script>
import app from "../components/app.vue"

export default {
    components:{
        app,
    
    },
    computed:{
        bottom(){
            return this.$store.state.bottom;
        }
    }
}
</script>
<style scoped>
.routerview{padding: 10px;}
.fixsing{position: fixed;left: 0;bottom: 0;}
</style>






