
import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        id: null,
        mvid: null,
        playid: null,
        player: false,
        songNew: "",
        songList: "",
        bottom:"0"
    },
    actions: {
        gotosing({ commit }, id) {
            console.log(id);
            commit('gotosing', id)
        },
        gotomv({ commit }, mvid) {
            commit('gotomv', mvid)
        },
        gotoplay({ commit }, playid) {
            commit("gotoplay", playid)
        }
    },
    mutations: {
        gotosing(state, id) {
            // ale.rt(id)
            state.id = id;
        },
        gotomv(state, mvid) {
            state.mvid = mvid;
        },
        gotoplay(state, playid) {
            state.playid = playid;
        },
        changePlayerState(state,data){
            state.player=data;
        },
        setBottomPX(state,data){
            state.bottom=data;
        }
    },
    getters: {
        getSongNew: state => {
            return state.songNew;
        },
        getsongList: state => {
            return state.songList;
        },
        watchplayid:state=>{
            return state.playid;
        },
        getPlayState: state => {
            return state.player;
        }
    }
})


export default store;


