<template>
  <div>
    <mt-swipe :auto="4000">
      <mt-swipe-item>
        <img src="http://p1.music.126.net/XSFisV58PP57m-q9qWx9XA==/109951163937364834.jpg">
      </mt-swipe-item>
      <mt-swipe-item>
        <img src="http://p1.music.126.net/skb2t1meqXTQKfQfe0BOZg==/109951163939357302.jpg">
      </mt-swipe-item>
      <mt-swipe-item>
        <img src="http://p1.music.126.net/CxUMXpy7kf_kq_kOzaxs-Q==/109951163939354657.jpg">
      </mt-swipe-item>
    </mt-swipe>
    <span class="music">推荐新音乐</span>
    <ul>
      <li @click="gotoplay(newsing.id)" v-for="(newsing,i) in newsings" :key="i">
        <img :src="newsing.song.album.blurPicUrl">
        <h3>{{newsing.name}}</h3>
        <p>{{newsing.song.artists[0].name}}</p>
      </li>
      <!-- <li>
        <img src="http://p2.music.126.net/XA5lU-lwNN2PDlW5gzj2Gg==/109951163919266456.jpg">
        <h3>好男儿志在四方</h3>
        <p>王龙华</p>
      </li>
      <li>
        <img src="http://p2.music.126.net/FjSi5kxC2rxXC-VhS4Gkig==/109951163909665795.jpg">
        <h3>Show Me Love</h3>
        <p>Above & Beyond</p>
      </li>
      <li>
        <img src="http://p2.music.126.net/rQw-f1jS2PIDnY2DoYlO7w==/109951163890222255.jpg">
        <h3>蜂鸟</h3>
        <p>吴青峰</p>
      </li>
      <li>
        <img src="http://p2.music.126.net/Ylf_pqhILuGhQjJ9VsXctA==/109951163881967978.jpg">
        <h3>只要有想见的人，就不是孤身一人</h3>
        <p>王源</p>
      </li>
      <li>
        <img src="http://p1.music.126.net/Z8tIFG2og0fmoOitW5gp1A==/109951163892179743.jpg">
        <h3>UTOPIA 梦托邦</h3>
        <p>王欣晨 Amanda</p> -->
      </li>
    </ul>
    <span class="music">推荐歌单</span>
    <ul>
      <li v-for="(item,i) in singer" :key="i" @click="gotosing(item.id)">
        <img :src="item.picUrl">
        <p class="singlist">{{item.name}}</p>
      </li>
    </ul>
    <span class="music">最新MV</span>
    <ul>
      <li v-for="(newmvs,i) in newmv" :key="i" @click="gotomv(newmvs.id)">
        <img :src="newmvs.cover">
        <h3>{{newmvs.name}}</h3>
        <p class="singlist">{{newmvs.briefDesc}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
import {mapState,mapActions,mapGetters,mapMutations} from "vuex";
import { Indicator, Toast } from "mint-ui";
export default {
  data() {
    return {
      singer: [],
      newmv:[],
      newsings:[]
    };
  },
  created(){

    Indicator.open({
      text: "加载中...",
      spinnerType: "fading-circle"
    });
    this.$http
      .get("http://120.78.191.141:3000/personalized")
      .then(res => {
        this.singer=(res.data.result.slice(0,6));
        // console.log(this.singer)
      });
      this.$http
      .get("http://120.78.191.141:3000/personalized/newsong")
      .then(res => {
        this.newsings=(res.data.result.slice(0,6));
        // console.log(this.singer)
      });
      this.$http
      .get("http://120.78.191.141:3000/mv/first?limit=6")
      .then(res => {
        this.newmv=(res.data.data);
         Indicator.close();
        // console.log(this.newmv)
      });
  },
  methods:{
    // ...mapActions(['gotosing']),
    // ...mapMutations(['gotosing']),
    gotosing(id){
      // alert(id);
      
      this.$store.dispatch('gotosing',id) 
      this.$router.push({name:'singlist'})
   },
   gotomv(mvid){
    //  console.log(mvid)
      this.$store.dispatch('gotomv',mvid) 
      this.$router.push({name:'video'})
   },
   gotoplay(playid){
      this.$store.dispatch('gotoplay',playid);
      this.$store.commit('changePlayerState',true);
      this.$store.commit('setBottomPX',"1.55rem");
      // this.$router.push({name:'play'})
   }
    
  }
    
};
</script>

<style scoped>
.mint-swipe,
img {
  width: 100%;
  height: 300px;
}
.music {
  font-weight: 800;
  margin: 20px 10px;
  border-bottom: 1px solid black;
  display: inline-block;
}
ul{
  overflow: hidden;
  margin-left: 20px;
}
li {
  width: 220px;
  margin-right: 5px;
 float:left;

}
li img {
  width: 220px;
  height: 220px;
}
h3{
  font-weight: 600;
  font-size: 24px;
  width: 220px;
  height: 30px;
  overflow: hidden;
}
p{font-size: 14px;color: darkgray}
.singlist{line-height: 21px;height: 42px;}
</style>



