<template>
    <mt-header :title="title">
        <div @click="goBack"  >
            <mt-button icon="back">返回</mt-button>
        </div>
        <mt-button icon="more" ></mt-button>
    </mt-header>
</template>

<script>
export default {
    methods:{
        goBack(){
            this.$router.go(-1);
        }
    },
   
}
</script>



