<template>
  <div>
    <h2>最新MV</h2>
    <ul>
      <li v-for="(newmvs,i) in newmv" :key="i"  @click="gotomv(newmvs.id)">
        <img :src="newmvs.cover">
        <h3>{{newmvs.name}}</h3>
        <p class="singlist">{{newmvs.briefDesc}}</p>
      </li>
    </ul>
    <h2>推荐MV</h2>
    <ul>
      <li v-for="(recommend,i) in recommendmv" :key="i"  @click="gotomv(recommend.id)">
        <img :src="recommend.cover">
        <h3>{{recommend.name}}</h3>
        <p class="singlist">{{recommend.artists[0].name}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
    data(){
        return{
            newmv:[],
            recommendmv:[]
        }
    },
    created(){
        this.$http
      .get("http://120.78.191.141:3000/mv/first?limit=8")
      .then(res => {
        this.newmv=(res.data.data);
        // console.log(this.newmv)
      });
      this.$http
      .get("http://120.78.191.141:3000/top/mv?limit=8")
      .then(res => {
        this.recommendmv=(res.data.data);
        // console.log(this.newmv)
      });
    },
    methods:{
      gotomv(mvid){
         this.$store.dispatch('gotomv',mvid) 
      this.$router.push({name:'video'})
      }
     
    }
}
</script>
<style scoped>
ul{
  overflow: hidden;
  margin-left: 20px;
}
li {
  width: 330px;
  margin-right: 5px;
 float:left;

}
li img {
  width: 330px;
  height: 220px;
}
h3{
  font-weight: 600;
  font-size: 24px;
  width: 220px;
  height: 30px;
  overflow: hidden;
}
p{font-size: 14px;color: darkgray}
.singlist{line-height: 21px;height: 42px;}
h2{padding: 10px;display: inline-block;border-bottom:1px solid red;margin-bottom: 10px;}
</style>





