<template>
  <div>
    <div class="singlisttop">
      <h3>
        <span @click="goback()">＜</span>歌单
      </h3>
      <img :src="singlist.coverImgUrl">
      <div class="title">
        <h4>{{singlist.name}}</h4>
        <p>
          <img :src="singlist.creator.avatarUrl">
          <span>{{singlist.creator.nickname}}</span>
        </p>
      </div>
    </div>
    <ul>
      <li v-for="(sing,i) in singlist.tracks" :key="i"  @click="gotoplay(sing.id)">
        <div class="numbel">{{i+1}}</div>
        <div class="sing_li">
          <p>{{sing.name}}　<span class="ietm">{{sing.alia[0]}}</span></p>
          <p class="singname">{{sing.ar[0].name}}</p>
        </div>
        
      </li>
    </ul>

    <play></play>

  </div>
</template>
<script>
import { mapState, mapActions, mapMutations } from "vuex";
import play from './../views/paly.vue'
export default {
  data() {
    return {
      singlist: [],
      id1:this.$store.state.id
    };
  },
  components:{
        play,
    },
  computed: {
    ...mapState(["id"]),
    ...mapState(["playid"]),
    
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    gotoplay(playid){
      this.$store.dispatch('gotoplay',playid);
      this.$store.commit('changePlayerState',true);
      this.$store.commit('setBottomPX',"1.55rem");
      // this.$router.push({name:'play'})
   },
  },
  create(){
    // console.log(this.$store.id);
    

  },
  mounted() {
    // console.log(this.$store.id);
    this.$http
      .get("http://120.78.191.141:3000/playlist/detail", {
        params: {
          id: this.id1
        }
      })
      .then(res => {
        this.singlist = res.body.playlist;

        console.log(this.$store.state.id)
      });
  }
};
</script>
<style scoped>
.singlisttop {
  background-color: rgb(194, 170, 170);
  overflow: hidden;
}
.singlisttop h3 {
  text-align: center;
  font-weight: 700;
}
.singlisttop h3 span {
  float: left;
  color: aqua;
  margin-left: 10px;
}
.singlisttop >img{width: 200px;height: 200px;margin: 10px;float: left;}
.title{float: left;margin-top: 20px;}
.title p{margin-top: 10px;}
.title p>img{width: 40xp;height: 40px;border-radius:50%;display: inline-block;}
.title p span{color: #fff}
li{overflow: hidden;}
.sing_li p{height: 40px;line-height: 40px;overflow: hidden;}
.sing_li{float: left;margin-left: 10px;width: 90%;}
.sing_li> .singname{border-bottom:1px solid #999 ;color: rgb(197, 182, 182)}/*no*/
li>.numbel{color:rgb(212, 185, 185);float: left;width: 40px;height:80px;line-height: 80px;text-align: center;}
</style>


    




