<template>
  <div>
    <div @click="goBack" slot="left" class="gotoback">
       返回
    </div>
    <mt-search v-model="value" @change="search" class="seach">
      <ul class="seachlist">
        <li v-for="(seachlist,i) in seach" :key="i" @click="gotomv(seachlist.id)">
          <sapn class="seachsing">{{seachlist.name}}</sapn>
          <span class="seachname">{{seachlist.artists[0].name}}</span>
        </li>
      </ul>
    </mt-search>
    <play></play>
  </div>
</template>


<script>
import play from './../views/paly.vue'
export default {
  data() {
    return {
      value: "",
      seach: []
    };
  },
  components:{
        play,
    },
  watch: {
    value: function(newVal) {
      this.$http
        .get("http://120.78.191.141:3000/search", {
          params: {
            keywords: newVal,
            type: 1,
            limit: 10
          }
        })
        .then(req => {
          this.seach = req.data.result.songs;
          console.log(this.seach);
        });
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    search() {},
    gotomv(playid){
      this.$store.dispatch('gotoplay',playid);
      this.$store.commit('changePlayerState',true);
      this.$store.commit('setBottomPX',"1.55rem");
      // this.$router.push({name:'play'})
   }
  }
};
</script>
<style scoped>

.gotoback {
  float: left;
  height:75px;
  line-height: 75px;
  padding: 0 10px;
}/*no*/

.seach{position: relative;}
.seachlist {
  position: absolute;
  left: 0;
  top: 1.2rem;
}
.seachlist li {
  height: 0.35rem;
  line-height: 0.35rem;
  overflow: hidden;
  padding: 5px;
}
.seachsing {
  float: left;
  width: 40%;
}
.seachname {
  float: right;
  width: 40%;
  color: #999;
}
</style>




