<template>
  <div v-if="getPlayState">
    
    <audio
      :src="'https://music.163.com/song/media/outer/url?id='+playid+'.mp3'"
      ref="player"
      autoplay
    ></audio>
    <div class="bottom_player">
      <img :src="details.pic">
      
      <div class="bottom_details">
        <img :src="playImg" class="palyCaozuo" @click="play"/>
        <h5>{{details.artist}}</h5>
        <p>{{details.title}}</p>
        <mt-range v-model="rangeValue" :barHeight="5" class="settime" @touchstart.native="touchendStart" @touchend.native="touchendEnd"> 
          <div slot="start" v-html="start"></div>
          <div slot="end" v-html="end"></div>
        </mt-range>
      </div>
    </div>

  </div>
</template>
<script>
import Aplayer from "vue-aplayer";
import { mapState, mapGetters } from "vuex";

export default {
  data() {
    return {
      showPlayer: false,
      url: null,
      details: {
        title: "",
        pic: "",
        artist: ""
      },
      mutex: true, //当一首音乐正在播放时，暂停其他音乐
      id: null,
      musicNew: null,
      currentTime: null, //歌曲时间
      isState: true,
      rangeValue: 0,
      start: "00:00",
      end: "00:00",
      LastTime:0,
      playImg:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1553788484681&di=c743062abb8fc25961e49db0e3d8b7cb&imgtype=0&src=http%3A%2F%2Ft.mylikesz.com%2Fskin%2Fzt%2Fwc_slz3%2Fimages%2Fplayer.png",
      isMove:true,
    };
  },
  components: {
    Aplayer
  },
  methods: {
    getSonglrc(id) {
      //请求歌词
      this.$http
        .get("http://120.78.191.141:3000/lyric",{
          params: {
            id: id
          }
        })
        .then(res => {
          // this.musicData.lrc = res.body.lrc.lyric;
          // console.log(res.body);
        });
    },
    //请求歌曲信息
    getSongNew(id) {
      this.$http
        .get("http://120.78.191.141:3000/song/detail", {
          params: {
            ids: id
          }
        })
        .then(res => {
          // console.log(res);
          this.details.title = res.body.songs[0].ar[0].name;
          this.details.artist = res.body.songs[0].name;
          this.details.pic = res.body.songs[0].al.picUrl;
        });
    },
    //格式化时间
    // format(interval) {
    //   interval = interval | 0;
    //   const minute = (interval / 60) | 0;
    //   const second = this._pad(interval % 60);
    //   return `${minute}:${second}`;
    // },
    touchendStart(){//按下
        this.isMove=false
    },
    touchendEnd(){//离开
      this.$refs.player.currentTime=(this.rangeValue/100)*this.LastTime;
      // console.log(this.$refs.player.currentTime)
      this.isMove=true;
    },
    //开启歌曲监听事件
    addEventListeners() {
      const self = this;
      //监听当前的播放位置发送改变
      self.$refs.player.addEventListener("timeupdate", self._currentTime);
      //监听开始播放时触发
      self.$refs.player.addEventListener('canplay', self._durationTime);
      //播放结束时触发
      // self.$refs.player.addEventListener('ended', self._endedPlay);
      //当前播放时触发
      self.$refs.player.addEventListener('play', self._Play);
      //当前暂停时触发
      self.$refs.player.addEventListener('pause', self._pause);
    },
    _currentTime() {
      var s = parseInt(this.$refs.player.currentTime);
      // console.log(s)
      let time = "";
      if (s > 0) {
        var min = Math.floor(s / 60) % 60;
        var sec = s % 60;
        if (min < 10) {
          time += "0";
        }
        time += min + ":";
        if (sec < 10) {
          time += "0";
        }
        time += sec;
        this.start = time;
        
      } else {
        self.start = "00:00";
      }
      // if (this.isMove) {
      //   this.sliderVal = (1 / this.LastTime) * s * 100;
      // }
      if(this.isMove){
      this.rangeValue=(s/this.LastTime)*100
      }
    },
    _durationTime(){
      let s = parseInt(this.$refs.player.duration);
      let time = "";
      this.LastTime=s;
       var min = Math.floor(s / 60) % 60;
        var sec = s % 60;
        time=min+":"+sec;
        this.end=time;
        console.log(time)
    },
    _Play(){
      this.playImg="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1553788663148&di=7d2962dbceb056a412b5aedb959f344c&imgtype=0&src=http%3A%2F%2Fpic.51yuansu.com%2Fpic2%2Fcover%2F00%2F48%2F90%2F581601be7cf78_610.jpg";
    },
    _pause(){
      this.playImg="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1553788484681&di=c743062abb8fc25961e49db0e3d8b7cb&imgtype=0&src=http%3A%2F%2Ft.mylikesz.com%2Fskin%2Fzt%2Fwc_slz3%2Fimages%2Fplayer.png"
    },
    play(){//播放，暂停
            let audios=this.$refs.player;
            if(audios.paused){
                this.$refs.player.play();
            }else{
                this.$refs.player.pause();
            }
        }
  },
  mounted() {
    // this.getSonglrc(this.playid);
    // this.getSongNew(this.playid);
  },
  computed: {
    ...mapState(["playid"]),
    ...mapGetters(["getPlayState"])
  },
  watch: {
    playid(id) {
      this.getSongNew(id);
      if (this.isState) {
        setTimeout(() => {
          this.addEventListeners();
        }, 20);
        this.isState = false;
      }
    },
    // updateTime(e) {
    //   this.currentTime = e.target.currentTime;
    // }
  }
};
</script>

<style scoped>
/* .aplayer-lrc{top: 100px;bottom: 100px;} */
/* .fixed{position: fixed;bottom: 0;left: 0;} */
.bottom_player {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 150px;
  z-index: 999;
  background: #eee;

}
.bottom_player>img {
  width: 25%;
  height: 150px;
  display: inline-block;
}
.bottom_details {
  display: inline-block;
  width: 72%;
}
.bottom_details>p{color:#888;font-size: 18px;position: relative;}
.palyCaozuo{position: absolute;left: 30px;top: 30px;width: 100px;height: 100px;border-radius: 50%;}
</style>


