<template>
  <div>
    <div class="singlist">
      <span class="sing" v-on:click="flag=!flag">{{cat}}</span>
      <div class="sings" v-show="!flag">
        <span
          v-for="(item,i) in singer"
          :key="i"
          :class="{active:cat==item.name} "
          @click="hotlabel(item.name,true)"
        >{{item.name}}</span>
      </div>
    </div>
    <h3>
      热门标签:
      <span
        v-for="(hot,i) in hotsing"
        :key="i"
        :class="{active:cat==hot.playlistTag.name}"
        @click="hotlabel(hot.playlistTag.name,false)"
      >{{hot.playlistTag.name}}</span>
    </h3>
    <div class="newhot">
      <span :class="{active:classname=='hot'}" @click="clickType('hot')">最热</span>
      <span :class="{active:classname=='new'}" @click="clickType('new')">最新</span>
    </div>
    <ul>
      <li v-for="(singli,i) in singlist" :key="i" @click="gotosing(singli.id)">
        <img v-lazy="singli.coverImgUrl">
        <p class="singlistname">{{singli.name}}</p>
        <span class="username">
          <img v-lazy="singli.creator.avatarUrl">
        </span>
        <span class="nickname">{{singli.creator.nickname}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import Vue from "vue";
import VueResource from "vue-resource";
Vue.use(VueResource);

import { Lazyload } from "mint-ui";

import { Indicator, Toast } from "mint-ui";

export default {
  data() {
    return {
      flag: true,
      singer: [],
      hotsing: [],
      classname: "hot",
      singlist: [],
      cat: "全部"
    };
  },
  created() {
    Indicator.open({
      text: "加载中...",
      spinnerType: "fading-circle"
    });
    this.$http.get("http://120.78.191.141:3000/playlist/catlist").then(res => {
      this.singer = res.data.sub;

      // console.log(this.singer)
    });
    this.$http.get("http://120.78.191.141:3000/playlist/hot").then(res => {
      this.hotsing = res.data.tags;
      // console.log(this.singer)
    });
    this.thishttp(this.classname, this.cat);
    // this.$http.get("http://120.78.191.141:3000/top/playlist",{
    //     params:{
    //             order:"new",
    //         }
    //   }).then(res => {
    //   this.singlist = res.data.playlists;
    //   console.log(this.classname)
    //   });
  },

  methods: {
    thishttp(order, cat) {
      // function thishttp(order="new",cat="全部"){

      // }
      Vue.http
        .get("http://120.78.191.141:3000/top/playlist", {
          params: {
            order: order,
            cat: cat
          }
        })
        .then(res => {
          this.singlist = res.data.playlists;
          Indicator.close();
          // console.log(this.classname)
        });
    },
    clickType(type) {
      this.classname = type;
      this.thishttp(this.classname, this.cat);
    },
    hotlabel(name, is) {
      this.cat = name;
      if (is) {
        this.flag = is;
      }
      this.thishttp(this.classname, this.cat);
    },
    gotosing(id){
      // alert(id);
      
      this.$store.dispatch('gotosing',id) 
      this.$router.push({name:'singlist'})
   },
  }
  //  computed:{
  //    classname:{

  //      setter(){
  //         this.$http.get("http://120.78.191.141:3000/top/playlist",{
  //         params:{
  //                 order:this.classname,
  //                 cat:this.cat,
  //             }
  //       }).then(res => {
  //       this.singlist = res.data.playlists;

  //       });
  //       },
  //       getter(){
  //         this.$http.get("http://120.78.191.141:3000/top/playlist",{
  //         params:{
  //                 order:this.classname,
  //             }
  //       }).then(res => {
  //       this.singlist = res.data.playlists;

  //       });
  //       }
  //    }
  //  },
  // updated(){
  //   // console.log(this.cat)
  //   this.$http.get("http://120.78.191.141:3000/top/playlist",{
  //       params:{
  //               order:this.classname,
  //           }
  //     }).then(res => {
  //     this.singlist = res.data.playlists;

  //     });
  // }
};
</script>
<style scoped>
span {
  margin-left: 10px;
}
.singlist {
  position: relative;
  margin: 30px 10px;
}
.sing {
  padding: 10px;
  border: 1px solid rgb(80, 204, 204); /*no*/
  display: inline-block;
  border-radius: 10px;
}
.sings {
  position: absolute;
  left: 0;
  top: 50px;
  padding: 10px;
  background-color: rgb(248, 247, 247);
}
.sings span {
  padding: 5px;
  display: inline-block;
  border: 1px solid #888; /*no*/
  margin-left: 10px;
  margin-top: 10px;
  border-radius: 10px;
}
h3 {
  margin-left: 10px;
}
.hotall {
  margin-left: 10px;
}
.active {
  background: rgb(100, 164, 224);
}
.newhot {
  margin: 10px 10px;
}
.newhot span {
  padding: 5px;
  border: deepskyblue;
  border-radius: 10px;
  display: inline-block;
}
.username {
  display: inline-block;
  width: 30px;
  height: 30px;
  margin: 0;
}
.username > img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.singlistname {
  font-size: 20px;
  width: 220px;
  overflow: hidden;
  height: 30px;
}
ul {
  overflow: hidden;
  margin-left: 20px;
}
li {
  width: 220px;
  margin-right: 5px;
  margin-top: 5px;
  float: left;
}
li > img {
  width: 220px;
  height: 220px;
}
.nickname {
  width: 180px;
  height: 30px;
  overflow: hidden;
  font-size: 18px;
  display: inline-block;
  margin: 0;
}
</style>





